export class CompanyClass {
  companyName: string;
  organizationalNumber: string;
  internalCompanyName: string;
  companyType: string;
  acquisition: boolean;
  acquisitionRegistrationDate: string;
  boardSeat: string;
  boardMembers: string[];
  auditor: string;
  parentCompany: string[];
  parentShares: string[];
  subsidiaries: string[];
  shareInSubsidiaries: string[];
  administeredBy: string;

  constructor(line: string) {
    const s = line.split(";");

    this.companyName = s[0];
    this.organizationalNumber = s[1];
    this.internalCompanyName = s[2];
    this.companyType = s[3];
    this.acquisition = s[4] === "YES";
    this.acquisitionRegistrationDate = s[5];
    this.boardSeat = s[6];
    this.boardMembers = s[7].split("|");
    this.auditor = s[8];
    this.parentCompany = s[9] === "" ? [] : s[9].split("|");
    this.parentShares = s[10].split("|");
    this.subsidiaries = s[11] === "" ? [] : s[11].split("|");
    this.shareInSubsidiaries = s[12].split("|");
    this.administeredBy = s[13];
  }
}
