import React, { useContext } from "react";
import { AppContext, AppContextType } from "../../core/appContext";
import { Company } from "../../components";
import s from "./OrgChartPage.module.css";

const OrgChartPage = () => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);
  const { companies } = context;
  const topCompany = companies.find(c => !c.parentCompany.length);

  if (!topCompany) {
    return null;
  }

  return (
    <div className={s.root}>
      <h2>Organisation Chart</h2>

      <div className={s.companyCount}>
        Companies: {context.companies.length}
      </div>

      <div>
        <Company
          level={1}
          position="middle"
          orgNumber={topCompany.organizationalNumber}
        />
      </div>
    </div>
  );
};

export default OrgChartPage;
