import React from "react";
import { Link, Route, Switch } from "react-router-dom";
import { AppContext, DefaultContext } from "./core/appContext";
import { HomePage, OrgChartPage } from "./pages";
import "./App.css";

const route = (path: string, page: any) => <Route path={path}>{page}</Route>;

function App() {
  return (
    <AppContext.Provider value={DefaultContext}>
      <div className="app">
        <div className="app-header">
          <img src="kpmg-logo.jpg" alt="kpmg" className="kpmg-logo" />

          <div>Entity Manager</div>

          <div className="menu">
            <Link to="/">Home</Link>
            <Link to="/orgchart">Chart</Link>
          </div>
        </div>

        <Switch>
          <Route path="/orgchart" render={() => <OrgChartPage />} />

          {route("/", <HomePage />)}
        </Switch>
      </div>
    </AppContext.Provider>
  );
}

export default App;
