import React, { useContext } from "react";
import { AppContext, AppContextType } from "../../core/appContext";
import s from "./Company.module.css";

const maxLevel = 10; // prevent accidental infinite loop

const getPosition = (index: number, count: number) => {
  if (count === 1) return "single";
  if (index === 0) return "left";
  if (index === count - 1) return "right";
  return "middle";
};

type Props = {
  level: number;
  orgNumber: string;
  position: string; // s.left, s.middle, s.right
};

// ----------
// COMPONENT:
const Company = ({ level, orgNumber, position }: Props) => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);
  const { companies } = context;

  const company = companies.find(c => c.organizationalNumber === orgNumber);
  if (!company) return <div>Orgnr not found: {orgNumber}</div>;

  const {
    companyName,
    internalCompanyName,
    parentShares,
    subsidiaries,
  } = company;

  const parentCount = company.parentCompany.length;

  const rootClass =
    s.root + " " + (level === 1 || subsidiaries.length === 0 ? s.noBorder : "");

  return (
    <div className={rootClass}>
      {/* CONNECTION LINES: */}
      {level > 1 && position !== "single" && (
        <div className={s.border}>
          <div className={position === "left" ? s.borderOff : s.borderOn}></div>
          <div
            className={position === "right" ? s.borderOff : s.borderOn}
          ></div>
        </div>
      )}
      {level > 1 && position !== "single" && <div>|</div>}
      {level > 1 && <div>{parentShares.join(" ")}</div>}
      {level > 1 && <div>â†“</div>}

      {/* COMPANY BOX: */}
      <div
        className={
          s.company + " " + (parentCount > 1 ? s.companyMultiParent : "")
        }
      >
        <div className={s.name}>{companyName}</div>
        <div>{internalCompanyName}</div>
        <div>{orgNumber}</div>
      </div>

      {/* CONNECTION LINES: */}
      {subsidiaries.length > 0 && <div>|</div>}

      {/* SUBSIDIARIES: */}
      {subsidiaries.length > 0 && level < maxLevel && (
        <div className={s.subsidiaries}>
          {subsidiaries.map((subsidiary, index) => (
            <Company
              key={subsidiary}
              level={level + 1}
              orgNumber={subsidiary}
              position={getPosition(index, subsidiaries.length)}
            />
          ))}
        </div>
      )}
    </div>
  );
};

export default Company;
