import React, { useState, useContext } from "react";
import { AppContext, AppContextType } from "../../core/appContext";
import { UploadDialog } from "../../components";
import s from "./HomePage.module.css";

const HomePage = () => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);

  // STATE:
  const [isUploadDialogOpen, setUploadDialogOpen] = useState(false);

  return (
    <div className={s.root}>
      <h2>Home</h2>

      <div className={s.companyCount}>
        Companies: {context.companies.length}
      </div>

      <button onClick={() => setUploadDialogOpen(true)}>
        Ladda upp en Excel-fil
      </button>

      {isUploadDialogOpen && (
        <UploadDialog onCloseClick={() => setUploadDialogOpen(false)} />
      )}
    </div>
  );
};

export default HomePage;
