import React, { useContext } from "react";
import { useHistory } from "react-router-dom";
import { CompanyClass } from "../../core/CompanyClass";
import { AppContext, AppContextType } from "../../core/appContext";
import s from "./UploadDialog.module.css";

type Props = {
  onCloseClick: (event: any) => void;
};

// ----------
// COMPONENT:
const UploadDialog = ({ onCloseClick }: Props) => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);

  const history = useHistory();

  // FUNCTIONS:
  // Assumption: file does not contain "|".
  // todo: check that it does not.
  const getLines = (fileContent: string) => {
    return (";" + fileContent)
      .replace(/\r/g, "")
      .split('""')
      .map(x => (x.startsWith(";") ? x : x.replace(/;/g, "|")))
      .join("")
      .split("\n") // get lines
      .slice(1) // skip the first
      .filter(line => line.length > 0) // skip empty
      .map(x => (x.startsWith('"') ? x.slice(1) : x))
      .map(x => (x.endsWith('"') ? x.slice(0, -1) : x));
  };

  // http://jsfiddle.net/Noitidart/zTe4j/3/
  const getFileContent = () => {
    return function (event: any) {
      const fileContent = event.target.result;

      if (fileContent.length > 0) {
        const lines = getLines(fileContent);
        const array = lines.map(line => new CompanyClass(line));
        context.companies = array;
      }

      history.push("/orgchart");
      onCloseClick(null);
    };
  };

  const onFileChange = (event: any) => {
    var file = event.target.files[0];
    var reader = new FileReader();
    reader.onload = getFileContent();
    reader.readAsText(file);
  };

  return (
    <div className={s.root}>
      <div id="myModal" className={s.modal}>
        <div className={s.content}>
          <div className={s.header}>Ladda upp en Excel-fil</div>

          <div className={s.inputBox}>
            <input type="file" onChange={onFileChange} />
          </div>

          <div className={s.footer}>
            <button onClick={onCloseClick}>StÃ¤nga</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default UploadDialog;
